<template>
  <div>
    <h1></h1>
     <baidu-map
      class="deviceaddmap"
      :center="centerposition"
      :zoom="15"
      :scroll-wheel-zoom="true"
      :inertial-dragging="true"
    >
      <bm-city-list anchor="BMAP_ANCHOR_TOP_LEFT"></bm-city-list>
      <bm-marker
        :position="{lng:lon,lat:lat}"
        :dragging="true"
        :raiseOnDrag="true"
        @dragging="dragging"
        @dragend="dragend"
      ></bm-marker>
    </baidu-map>



    <div style="text-align: left;">
      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="title"
          style="width:200px"
        ></el-input>
        <span style="color:red;">*</span>
      </p>
      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="desc"
          style="width:200px"
        ></el-input>
      </p>

      <p>
        <span class="mytitle"></span>
        <el-select
          v-model="companyid"
          clearable
          placeholder=""
          style="width:200px;margin-right:20px;"
        >
          <el-option
            v-for="item in companylist"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <span style="color:red;">*</span>
      </p>

      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="tags"
          style="width:200px"
        ></el-input>
        <span class="notice"></span>
      </p>
      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="lon"
          style="width:200px"
        ></el-input>
      </p>

      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="lat"
          style="width:200px"
        ></el-input>
      </p>

      <p>
        <span class="mytitle"></span>
        <el-select
          v-model="private_str"
          clearable
          placeholder=""
          style="width:200px"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </p>
      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="auth_info"
          style="width:200px"
        ></el-input>
        <span class="notice">512</span>
      </p>

      <p>
        <span class="mytitle"></span>
        <el-input
          placeholder=""
          v-model="other_str"
          style="width:200px"
        ></el-input>
        <span class="notice">json</span>
      </p>

    </div>
    <div style="text-align: center;">
      <el-button
        type="primary"
        icon="el-icon-check"
        @click="handleSummit()"
      ></el-button>
      <el-button
        type="success"
        icon="el-icon-caret-left"
        @click="handleback()"
      ></el-button>
    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      centerposition:{lng:106.592522,lat:29.570243},
      companylist: [],
      companyid: "",

      title: "",
      desc: "",
      tags: "",
      //    tagsArray:[],
      //    location:{},
      lon: 106.592522,
      lat: 29.570243,
      //    private:true,
      private_str: "",
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "0",
          label: ""
        }
      ],
      auth_info: "",
      other_str: ""
      //    other:{}
    };
  },
  // -------------------------------------------------------
  methods: {

    dragging(e) {
      // console.log(e);
      this.lon = e.point.lng;
      this.lat = e.point.lat;
    },

    dragend(e){
      this.centerposition={lng:e.point.lng,lat:e.point.lat}

    },




    handleback() {
      this.$router.go(-1);
    },

    handleSummit() {
      console.log("");
      var _this = this;
      var p = this.checkvalues();
      console.log(p);
      p.then(
        function(res) {
          console.log("resolve:" + res);
          _this.adddevice();
        },
        function(res) {
          console.log(res);
        }
      ).catch(err => {
        console.log(err);
      });
    },

    adddevice() {
      console.log("adddevice");
      let tagsArray = [];
      let location = {};
      let other = {};
      let myprivate = true;
      if (this.tags != "" && this.tags != null && this.tags != undefined)
        tagsArray = this.tags.split(",");
      if (this.lon != "" || this.lat != "") {
        location.lon = this.lon;
        location.lat = this.lat;
      }
      if (this.private_str == "0") myprivate = false;
      else myprivate = true;

      let other_trim = this.other_str.replace(/\s+/g, "");
      if (other_trim != "" && other_trim != null && other_trim != undefined) {
        other = JSON.parse(this.other_str);
      }

      let summitdata = {};
      summitdata.title = this.title;
      if (this.desc != "") summitdata.desc = this.desc;
      if (tagsArray.length != 0) summitdata.tags = tagsArray;
      if (Object.keys(location).length != 0) summitdata.location = location;
      summitdata.private = myprivate;
      if (this.auth_info != "") summitdata.auth_info = this.auth_info;
      if (Object.keys(other).length != 0) summitdata.other = other;

      this.$axios({
        method: "post",
        url: "/iot/devices",
        data: summitdata
      }).then(data => {
        if (data.data.error == "succ") {
          let paramsadddevice = new URLSearchParams();
          paramsadddevice.append("deviceid", data.data.data.device_id);
          paramsadddevice.append("companyid", this.companyid);
          paramsadddevice.append("title", this.title);
          this.$axios({
            method: "post",
            url: "/api/device/adddevice",
            params: paramsadddevice
          }).then(data2 => {
            if (data2.data.code == 0) {
              this.$message({
                type: "success",
                message: "ID" + data.data.data.device_id
              });
            } else {
              //onenet
              this.$message({
                type: "error",
                message: ""
              });
              this.$axios
                .delete("/iot/devices/" + data.data.data.device_id)
                .then(data3 => {
                  if (data.data.error == "succ") {
                    this.$message({
                      type: "error",
                      message: ""
                    });
                  } else {
                    this.$message({
                      type: "error",
                      message: "onenet"
                    });
                  }
                });
            }
          }).catch(err=>{
              this.$axios
                .delete("/iot/devices/" + data.data.data.device_id)
                .then(data3 => {
                  if (data.data.error == "succ") {
                    this.$message({
                      type: "error",
                      message: "..."
                    });
                  } else {
                    this.$message({
                      type: "error",
                      message: "onenet"
                    });
                  }
                });


          });
        } else {
          this.$message({
            type: "error",
            message: "" + data.data.error
          });
        }
      });
    },

    checkvalues() {
      let other_trim = this.other_str.replace(/\s+/g, "");
      if (this.title == "" || this.title == null || this.title == undefined) {
        this.$message({
          type: "error",
          message: ""
        });
        return Promise.reject(-1);
      } else if (
        this.companyid == "" ||
        this.companyid == null ||
        this.companyid == undefined
      ) {
        this.$message({
          type: "error",
          message: ""
        });
        return Promise.reject(-1);
      } else if (
        other_trim != "" &&
        other_trim != null &&
        other_trim != undefined &&
        !this.isJSON(this.other_str)
      ) {
        this.$message({
          type: "error",
          message: "JSON"
        });
        return Promise.reject(-1);
      } else {
        return Promise.resolve(1);
      }
    },

    isJSON(str) {
      if (typeof str == "string") {
        try {
          var obj = JSON.parse(str);
          if (typeof obj == "object" && obj) {
            return true;
          } else {
            return false;
          }
        } catch (e) {
          console.log("error" + str + "!!!" + e);
          return false;
        }
      }
      console.log("It is not a string!");
    }
    // -----------------------------------------------------
  },

  created() {
    let roletype = sessionStorage.getItem("roletype");
    let companyparams = new URLSearchParams();
    if (roletype != 1) {
      companyparams.append("companyid", sessionStorage.getItem("companyid"));
    }
    this.$axios
      .post("/api/company/getcompanylist", companyparams)
      .then(data => {
        let companyList = data.data.data;
        companyList.forEach(element => {
          if (element.isvalid == 1)
            this.companylist.push({
              label: element.companyname,
              key: element.id,
              value: element.id
            });
        });
      });
  }
};
</script>
<style scope="scoped">
.mytitle {
  display: inline-block;
  width: 80px;
  text-align: right;
  color: #606266;
}

.notice {
  font-size: 5px;
  width: 100%;
  text-align: left;
  color: red;
  margin-left: 8px;
}

.deviceaddmap {
  width: 50%;
  height: 400px;
  position: relative;
  float: right;
  margin-top: 10px;
  margin-right: 30px;
}
</style>


