import Main from '@/components/Main'
import Receiver from '@/components/receiver/Receiver'


const ReceiverRouter = [{
    path: '/receiver',
    name: '',
    component: Main,
    iconCls: 'fa fa-microchip',
    leaf:false,
    meta:{
        permission:'receiver:view'
    },
    children: [{
        path: '/receiver/receiver',
        meta:{
            permission:'receiver:view'
        },
        component: Receiver,
        name: ''
    }]
}]

export {
    ReceiverRouter
}


