<template>
    <div>

    <baidu-map
      class="deviceDTOmap"
      :center="{lng:dto.location.lon,lat:dto.location.lat}"
      :zoom="15"
      :scroll-wheel-zoom="true"
      :inertial-dragging="true"
      v-if="dto.location!=undefined"
    >
      <bm-marker
        :position="{lng:dto.location.lon,lat:dto.location.lat}"
        :dragging="false"
      ></bm-marker>
    </baidu-map>



              <p>
                <span class="mytitlec">ID{{dto.id}}</span> 
              </p>
              <p>
                <span class="mytitlec">{{dto.title}}</span> 
              </p>
              <p>
                <span class="mytitlec">  </span> 
                <i v-if="dto.online" class="fa fa-circle" style="font-size:20px;color:green;"></i>
                <span class="mytitlec" v-if="dto.online"></span> 
                <i v-if="!dto.online" class="fa fa-circle" style="font-size:20px;color:red;"></i>
                <span class="mytitlec" v-if="!dto.online"></span> 
              </p>

              <p>
                <span class="mytitlec">{{dto.create_time}}</span> 
              </p>
              <p>
                <span class="mytitlec">{{dto.desc}}</span> 
              </p>
              <p>
                <span class="mytitlec">{{dto.auth_info}}</span> 
              </p>
             
              <p>
                <span class="mytitlec">  </span> 
                <span class="mytitlec" v-if="dto.private"></span> 
                <span class="mytitlec" v-if="!dto.private"></span> 
              </p>
              <p>
                <span class="mytitlec">{{dto.protocol}}</span> 
              </p>

              <p v-if="dto.location!=undefined">
                <span class="mytitlec">{{dto.location.lat}} ,</span> 
                <span class="mytitlec">{{dto.location.lon}}</span> 
              </p>

            <p v-if="dto.datastreams!=undefined&&dto.datastreams.size!=0"> 
              <span class="mytitlec"></span>   
              <el-table :data="dto.datastreams" stripe style="width: 100%; margin-top:20px">
                <el-table-column prop="id" label="ID" width="180"></el-table-column>
                <el-table-column prop="unit" label="" width="180"></el-table-column>
                <el-table-column prop="unit_symbol" label="" width="180"></el-table-column>
                <el-table-column prop="create_time" label=""></el-table-column>
              </el-table>
           </p>
           <p  style="text-align:center;">
               <el-button type="success" icon="el-icon-arrow-left" @click="handleBack()"></el-button>
           </p>
    </div>
</template>


<script>
    export default {
        data() {
            return {
                dto:{}
            }
        },
        methods:{
            handleBack(){
            this.$router.go(-1);
            }
        },
        created() {
            this.row = this.$route.query.row
            let device_id=''
            if (this.row.id != "" && this.row.id != null && this.row.id != undefined) {
              sessionStorage.setItem("device_id", this.row.id);
              device_id=this.row.id
            }else{
             device_id=sessionStorage.getItem("device_id")
            }
            this.$axios.get("/iot/devices/"+device_id).then(
                data=>{
                    console.log(data)
                    this.dto=data.data.data
                }
            )
        }





    }
</script>
<style scope="scoped">
.mytitlec {
  font-size: 20px;
  /* display: inline-block;  */
  width: 100%;
  text-align: left;
  margin-left: auto;
  color: #343536;
}

.deviceDTOmap {
  width: 50%;
  height: 400px;
  position: relative;
  float: right;
  margin-top: 10px;
  margin-right: 30px;
}
</style>


