import Vue from 'vue'
import Router from 'vue-router'

import Dashboard from '@/components/Dashboard'
import Main from '@/components/Main'
import Login from '@/components/Login'
import store from '../store/Store' 

// import ContractEdit from '@/components/system/ContractEdit'



Vue.use(Router)




let routes = [
    {
    path:'/main',
    component: Main,
    hidden: true,
    redirect:'/dashboard',
    name:"",
    children: [{
        path: '/dashboard',
        component:Dashboard,
        name:''
    }]
}
]



// routes.push({
//     path:'/',
//     name:'',
//     redirect:'/login',
//     hidden: true
//     }
// )

routes.push({
    path:'/login',
    name:'',
    component:Login,
    hidden: true
    }
)


import {
    SystemRouter
} from './system'

import {
    DeviceRouter
} from './device'

import {
    ReceiverRouter
} from './receiver'

import {
    CompanyRouter
} from './company'


for (let i in SystemRouter){
    routes.push(SystemRouter[i])
}

for (let i in CompanyRouter){
    routes.push(CompanyRouter[i])
} 


for (let i in DeviceRouter){
    routes.push(DeviceRouter[i])
}

for (let i in ReceiverRouter){
    routes.push(ReceiverRouter[i])
}




const router = new Router({

    // base: '/iot/',
    scrollBehavior: () => ({ y: 0 }),
    routes: routes,
    // mode:"history"
})



router.beforeEach(function (to, from, next){

    let userid=sessionStorage.getItem("userid_login")//
    const auth = store.getters.getisloginById(userid);//
    //  debugger
    //  console.log(to.path);
    //  console.log(auth);
    if(auth==null||!auth.islogin||auth==undefined||auth.islogin==undefined||auth.id==undefined){
        if(to.path=='/login'){
            next();
        }else{
            next('/login');
        }

    }else{
        if(to.path=='/login'||to.path=='/'){
            alert("")    
            next('/main');
        }else{
            next();
        }
    }
})


export default router;




