<template>
    <div>
        <p style="text-align: right;">
            <el-button type="primary" icon="el-icon-circle-plus" @click="addrole()" v-hasPermission="'system:role:add'"></el-button>
        </p>
        <el-table :data="tableData.filter(data => !search || data.rolename.toLowerCase().includes(search.toLowerCase()))" :header-cell-style="getRowClass" :row-style="tableRowStyle">

            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="ID">
                            <span>{{ props.row.id }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.rolename }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.description }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span v-if="props.row.roletype==1"></span>
                            <span v-if="props.row.roletype!=1"></span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.createtime }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.updatetime }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span v-if="props.row.isvalid==1"></span>
                            <span v-if="props.row.isvalid!=1"></span>
                        </el-form-item>
                        <el-form-item label="">
                            <span v-for="item in props.row.authList">{{item}}<br></span>
                        </el-form-item>

                    </el-form>
                </template>
            </el-table-column>

            <el-table-column label="ID" prop="id"></el-table-column>
            <el-table-column label="" prop="description"></el-table-column>
            <el-table-column label="" prop="roletype"></el-table-column>
            <el-table-column label="" prop="rolename"></el-table-column>

            <el-table-column align="right">
                <template slot="header" slot-scope="slot">
                    <el-input v-model="search" size="mini" placeholder="" v-hasPermission="'system:user:view'" />
                    
                </template>

                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)" v-hasPermission="'system:role:update'"></el-button>

                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" v-hasPermission="'system:role:delete'"></el-button>
                </template>
            </el-table-column>
        </el-table>

    </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      search: ""
    };
  },
  methods: {



    handleEdit(index,row){
      console.log("")
      this.$router.push({path: '/system/RoleEdit', query: {index:index,row:row}})
    },


    handleDelete(index, row) {
      this.$confirm(
        "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).then(() => {
        let params = new URLSearchParams();
        if (row.id != "" && row.id != null) params.append("roleid", row.id);
        this.$axios.post("/api/role/removerolebyid", params).then(data => {
          if (data.data.data == 1) {
            this.tableData.splice(index, 1);
            console.log("");
          } else {
            console.log("");
          }
        });
      }).catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },


    addrole(){
        console.log("")
        this.$router.push({path: '/system/RoleAdd'})
    },

    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return "background-color: #F7F6Fd;color: #606266;font-weight: 500;";
      }
    },

    tableRowStyle({ row, rowIndex }) {
      return "background-color: #F7F6Fd";
    }
  },
  created() {
    let roletype = sessionStorage.getItem("roletype");
    let roleparams = new URLSearchParams();
    roleparams.append("roletype", roletype);
    this.$axios({
      method: "post",
      url: "/api/role/rolelist",
      params: roleparams
    }).then(data => {
      this.tableData = data.data.data;
      console.log(this.tableData);
    });
  }
};
</script>
<style scoped>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.roletitle {
  display: inline-block;
  width: 80px;
  text-align: right;
  color: #606266;
}
</style>


