<template>

    <div>
        <div id="searchcompany">
          ID: <el-input  placeholder="ID" v-model="companyid" clearable  style="width:200px"></el-input>
          : <el-input  placeholder="" v-model="companyname" clearable  style="width:200px"></el-input>
          : <el-select  v-model="isvalid" clearable placeholder="" style="width:150px">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>  

                <el-button type="primary" icon="el-icon-search" @click="handleSearch()"></el-button>

                <el-button type="primary" icon="el-icon-circle-plus" @click="addCompany()" v-hasPermission="'company:add'"></el-button>
        </div>

      

      <el-table
      :data="tableData.filter(data => !search || data.id.toLowerCase().includes(search.toLowerCase()))"
      :header-cell-style="getRowClass"
      :row-style="tableRowStyle">

      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="ID">
              <span>{{ props.row.id }}</span>
            </el-form-item>

            <el-form-item label="">
              <span>{{ props.row.companyname }}</span>
            </el-form-item>

            <el-form-item label="APIKEY">
              <span v-if="props.row.isvalid==1"></span>
              <span v-if="props.row.isvalid==0"></span>
            </el-form-item>


            <el-form-item label="APIKEY">
              <span>{{ props.row.apikey }}</span>
            </el-form-item>

            <el-form-item label="">
              <span>{{ props.row.createtime }}</span>
            </el-form-item>

            <el-form-item label="">
              <span>{{ props.row.updatetime }}</span>
            </el-form-item>

          </el-form>
        </template>
      </el-table-column>

      <el-table-column label="ID" prop="id"></el-table-column>

      <el-table-column label="" prop="companyname"></el-table-column>

      <el-table-column label="" prop="isvalid">
          <template slot-scope="scope">
            <span v-if="scope.row.isvalid==1"></span>
            <span v-if="scope.row.isvalid==0"></span>
        </template>
      </el-table-column>

      <el-table-column align="right">
        <template slot="header" slot-scope="slot">
          <el-input v-model="search" size="mini" placeholder="ID"/>
          <br>
        </template>

        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            circle
            title=""
            @click="handleEdit(scope.$index, scope.row)"
            v-hasPermission="'company:update'"
          ></el-button>
         
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            title=""
            @click="handleDelete(scope.$index, scope.row)"
            v-hasPermission="'company:delete'"
          ></el-button>

          <el-button
            v-show="scope.row.isvalid==1"
            size="mini"
            type="warning"
            icon="fa fa-lock"
            circle
            title=""
            @click="handleUnlockOrLock(scope.$index, scope.row,0)"
            v-hasPermission="'company:update'"
          ></el-button>

          <el-button
            v-show="scope.row.isvalid==0"
            size="mini"
            type="warning"
            icon="fa fa-unlock"
            circle
            title=""
            @click="handleUnlockOrLock(scope.$index, scope.row,1)"
            v-hasPermission="'company:update'"
          ></el-button>

        </template>
      </el-table-column>
    </el-table>

</div>
</template>
<script>
export default {
    data() {
        return{
            tableData:[],
            search:'',
            companyid:'',
            companyname:'',
            isvalid: '',
            options: [{
                value: '1',
                label: ''
                }, {
                value: '0',
                label: ''
            }]
            
            }
    },
methods: {

    handleUnlockOrLock(index,row,isvalid){
      console.log("/")
      let params = new URLSearchParams();
      params.append("companyid",row.id);
      params.append("isvalid",isvalid);
      this.$axios.post("/api/company/lockcompanybyid",params).then(data=>{
        if(data.data.code==0){
          this.tableData[index].isvalid=isvalid
          this.$message({
          type: "success",
          message: ""
          });
        }else{
          this.$message({
          type: "error",
          message: ""
          });

        }
      })
    },

    // handleUnlock(index,row){
    //   console.log("")
    //   let params = new URLSearchParams();
    //   params.append("companyid",row.id);
    //   params.append("isvalid",1);
    //   this.$axios.post("/api/company/lockcompanybyid",params).then(data=>{
    //      if(data.data.code==0){
    //       this.tableData[index].isvalid=1
    //       this.$message({
    //       type: "success",
    //       message: ""
    //       });
    //     }else{
    //       this.$message({
    //       type: "error",
    //       message: ""
    //       });

    //     }       
    //   })
    // },


    handleDelete(index,row){
       console.log("")

      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(()=>{

        let params = new URLSearchParams() 
        if(row.id!=''&&row.id!=null&&row.id!=undefined)
          params.append('companyid', row.id)
          this.$axios.post('/api/company/deletecompany',params).then(data=>{
              console.log(data);
              if(data.data.code==0){
                this.$message({
                type: "success",
                message: data.data.msg
                });
                this.tableData.splice(index,1)
              }else{
                this.$message({
                type: "error",
                message: data.data.msg
                });
              }
          }) 

        }).catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });


      

      
    },
    handleEdit(index,row){
      console.log("")
      this.$router.push({ path: "/company/companyupdate",query: { index: index, row: row }});
    },

     addCompany(){
      console.log("")
      this.$router.push({ path: "/company/companyadd" });
    },

    handleSearch(){
      let params= new URLSearchParams
      if(this.companyid!=''&&this.companyid!=null&&this.companyid!=undefined)
        params.append("companyid",this.companyid)
      if(this.companyname!=''&&this.companyname!=null&&this.companyname!=undefined)
        params.append("companyname",this.companyname)
      if(this.isvalid!=''&&this.isvalid!=null&&this.isvalid!=undefined)
        params.append("isvalid",this.isvalid)
      this.$axios.post("/api/company/getcompanylist",params).then(data=>{
        console.log(data)
        this.tableData=data.data.data
      })


      console.log("")
    },
   

    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return "background-color: #F7F6Fd;color: #606266;font-weight: 500;";
      }
    },

    tableRowStyle({ row, rowIndex }) {
      return "background-color: #F7F6Fd";
    },

},

created(){
    this.$axios.post("/api/company/getcompanylist").then(data=>{
        console.log(data)
        this.tableData=data.data.data
    })
}



}

</script>
<style scope="scoped">

#searchcompany {
  color: #606266;
  font-size: 16px;
  text-align: left;
  margin-top: 20px;
  margin-right: auto;
  margin-bottom: 20px;
  margin-left: auto;
}

</style>


