<template>
  <div>

    <div id="search">
      ID: <el-input
        placeholder="ID"
        v-model="deviceID"
        clearable
        style="width:200px;margin-right:20px;"
      ></el-input>
      <el-input
        placeholder=""
        v-model="title"
        clearable
        style="width:200px;margin-right:20px;"
      ></el-input>
      :
      <el-select
        v-model="online"
        clearable
        placeholder=""
        style="width:150px;margin-right:20px;"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>

      <el-button
        type="primary"
        icon="el-icon-search"
        @click="handleSearch()"
      ></el-button>
      <el-button
        type="primary"
        icon="el-icon-circle-plus"
        @click="addDevice()"
        v-hasPermission="'device:add'"
      ></el-button>

      <p v-show="roletype=='1'">
        <span></span>
        <el-select
          v-model="companyid"
          clearable
          placeholder=""
          style="width:150px;margin-right:20px;"
        >
          <el-option
            v-for="item in companylist"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </p>
    </div>

    <el-table
      :data="tableData.filter(data => !search || data.id.toLowerCase().includes(search.toLowerCase()))"
      :header-cell-style="getRowClass"
      :row-style="tableRowStyle"
    >

      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form
            label-position="left"
            inline
            class="demo-table-expand"
          >
            <el-form-item label="">
              <span>{{ props.row.title }}</span>
            </el-form-item>
            <el-form-item label="ID">
              <span>{{ props.row.id }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.protocol }}</span>
            </el-form-item>
            <el-form-item label="">
              <span v-if="props.row.online"></span>
              <span v-if="!props.row.online"></span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.create_time }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.auth_info }}</span>
            </el-form-item>

            <el-form-item label="">
              <span>{{ props.row.desc }}</span>
            </el-form-item>

            <el-form-item label="">
              <span v-if="props.row.location!=undefined">{{ props.row.location.lat }}</span>
              <span v-if="props.row.location!=undefined">{{ props.row.location.lon }}</span>
            </el-form-item>
            <el-form-item label="">
              <span v-if="props.row.private"></span>
              <span v-if="!props.row.private"></span>
            </el-form-item>

            <el-form-item label="">
              <span v-for="item in props.row.tags">{{ item }}</span>
            </el-form-item>

          </el-form>
        </template>
      </el-table-column>

      <el-table-column
        label="ID"
        prop="id"
      >
      </el-table-column>
      <el-table-column
        label=""
        prop="title"
      >
      </el-table-column>
      <el-table-column
        label=""
        prop="online"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.online"></span>
          <span v-else-if="!scope.row.online"></span>
        </template>
      </el-table-column>

      <el-table-column
        label=""
        prop="private"
      >
        <template slot-scope="scope">
          <span v-if="scope.row.private"></span>
          <span v-else-if="!scope.row.private"></span>
        </template>
      </el-table-column>

      <el-table-column align="right">
        <template
          slot="header"
          slot-scope="slot"
        >
          <el-input
            v-model="search"
            size="mini"
            placeholder="ID"
          /><br>
        </template>

        <template slot-scope="scope">
          <el-button
            size="mini"
            icon="el-icon-document"
            circle
            title=""
            @click="handleView(scope.$index, scope.row)"
            v-hasPermission="'device:view'"
          ></el-button>
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            circle
            title=""
            @click="handleEdit(scope.$index, scope.row)"
            v-hasPermission="'device:update'"
          ></el-button>
          <el-button
            size="mini"
            type="success"
            icon="el-icon-sort"
            circle
            title=""
            @click="handleSendcmd(scope.$index, scope.row)"
            v-hasPermission="'device:cmd'"
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            title=""
            @click="handleDelete(scope.$index, scope.row)"
            v-hasPermission="'device:delete'"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="text-align:center;margin-top:20px"
      background
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      @prev-click="handlePrePage"
      @next-click="handleNextPage"
      :current-page="this.page"
      :page-size="this.per_page"
      :pager-count="11"
      :total="this.total_count"
    >
    </el-pagination>
  </div>
</template>


<script>
export default {
  data() {
    return {
      roletype: "",
      tableData: [],
      search: "",
      deviceID: "",
      title: "",
      online: "",
      total_count: 0,
      page: 1,
      per_page: 8,
      companylist: [],
      companyid: "",
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "0",
          label: ""
        }
      ]
      // isvalid: ''
    };
  },
  methods: {
    handleCurrentChange(val) {
      console.log(":" + val);
      this.page = val;
      this.refesh();
    },

    handlePrePage() {
      console.log("");
      this.page = this.page - 1;
    },

    handleNextPage() {
      console.log("");
      this.page = this.page + 1;
    },

    handleSearch() {
      this.page = 1;
      this.refesh();
    },

    handleEdit(index, row) {
      console.log("");
      this.$router.push({
        path: "/device/edit",
        query: { index: index, row: row }
      });
    },

    handleSendcmd(index, row) {
      console.log("");
      this.$router.push({
        path: "/device/sendcmd",
        query: { index: index, row: row }
      });
    },

    handleView(index, row) {
      this.$router.push({
        path: "/device/dto",
        query: { index: index, row: row }
      });
      console.log("");
    },

    addDevice() {
      console.log("");
      this.$router.push({ path: "/device/adddevice" });
    },

    handleDelete(index, row) {
      console.log("");

      let deviceID = row.id;
      let url = "/iot/devices/" + deviceID;
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let params = new URLSearchParams();
          params.append("deviceid", deviceID);
          this.$axios({
            method: "post",
            url: "/api/device/deletedevice",
            params: params
          }).then(data => {
            if (data.data.code == 0) {
              this.$axios.delete(url).then(data => {
                if (data.data.error == "succ") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.refesh();
                } else {
                  this.$message({
                    type: "error",
                    message: ""
                  });
                }
              });
            } else {
              this.$message({
                type: "error",
                message: ""
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    getRowClass({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return "background-color: #F7F6Fd;color: #606266;font-weight: 500;";
      }
    },

    tableRowStyle({ row, rowIndex }) {
      return "background-color: #F7F6Fd";
    },

    refesh() {
      console.log("");
      let params = new URLSearchParams();
      params.append("pagesize", this.per_page);
      params.append("pagenum", this.page);
      if (this.title != "" && this.title != null && this.title != undefined)
        params.append("title", this.title);
      if (
        this.deviceID != "" &&
        this.deviceID != null &&
        this.deviceID != undefined
      )
        params.append("device_id", this.deviceID);
      if (
        this.companyid != "" &&
        this.companyid != null &&
        this.companyid != undefined
      )
        params.append("companyid", this.companyid);
      this.$axios({
        method: "post",
        url: "/api/device/getdevieidlist",
        params: params
      }).then(data => {
        this.total_count = data.data.data.total;
        let deviceListStr = "";

        if (data.data.data.list.length != 0) {
          data.data.data.list.forEach(element => {
            deviceListStr += element + ",";
          });
          deviceListStr = deviceListStr.replace(/^,+/, "").replace(/,+$/, "");
          let paramsl = {};
          paramsl.device_id = deviceListStr;
          if (this.online != "" && this.online == "1") paramsl.online = true;
          if (this.online != "" && this.online == "0") paramsl.online = false;
          this.$axios.get("/iot/devices", { params: paramsl }).then(data => {
            console.log(data);
            this.tableData = data.data.data.devices;
          });
        } else {
          this.tableData = [];
        }
      });
    }
  },

  created() {
    let params = new URLSearchParams();
    params.append("pagesize", this.per_page);
    params.append("pagenum", this.page);

    this.$axios({
      method: "post",
      url: "/api/device/getdevieidlist",
      params: params
    }).then(data => {
      this.total_count = data.data.data.total; //
      let deviceListStr = "";
      data.data.data.list.forEach(element => {
        deviceListStr += element + ",";
      });
      deviceListStr = deviceListStr.replace(/^,+/, "").replace(/,+$/, "");
      let paramsl = {};
      paramsl.device_id = deviceListStr;
      this.$axios.get("/iot/devices", { params: paramsl }).then(data => {
        this.tableData = data.data.data.devices;
      });
    });

    let roletype = sessionStorage.getItem("roletype");
    let companyparams = new URLSearchParams();
    if (roletype != 1) {
      companyparams.append("companyid", sessionStorage.getItem("companyid"));
    }
    this.$axios
      .post("/api/company/getcompanylist", companyparams)
      .then(data => {
        let companyList = data.data.data;
        companyList.forEach(element => {
          if (element.isvalid == 1)
            this.companylist.push({
              label: element.companyname,
              key: element.id,
              value: element.id
            });
        });
      });
    this.roletype = sessionStorage.getItem("roletype");
  }
};
</script>

<style scope="scoped">
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

/* #el-input{
    width: 200px;
    margin-right: 20px;
  }
   */

#search {
  color: #606266;
  font-size: 16px;
  text-align: left;
  margin-top: 20px;
  margin-right: auto;
  margin-bottom: 20px;
  margin-left: auto;
}
</style>


