<template>
  <div>
    <h1></h1>
    <div style="text-align: left;">

      <p>
        <span class="myusertitle"></span>
        <el-input placeholder="" v-model="loginname" style="width:200px"></el-input>
        <span style="color:red;">*</span>
      </p>

      <p>
        <span class="myusertitle"></span>
        <el-input placeholder="" v-model="password" style="width:200px"></el-input>
        <span style="color:red;">*</span>
      </p>

      <p>
        <span class="myusertitle"></span>
        <el-input placeholder="" v-model="phonenum" style="width:200px"></el-input>
        <span style="color:red;">*</span>
      </p>
      <p>
        <span class="myusertitle"></span>
        <el-input placeholder="" v-model="name" style="width:200px"></el-input>
        <span style="color:red;">*</span>
      </p>

      <!-- <p>
                <el-transfer
                    filterable
                    filter-placeholder=""
                    v-model="right"
                    :data="left"
                    style="margin-left:30px">
                </el-transfer>
            </p> -->

      <p>
        <span class="myusertitle"></span>
        <el-select v-model="companyid" filterable placeholder="" style="width:200px">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span style="color:red;">*</span>
      </p>

      <p>
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
        <div style="margin: 15px 0;"></div>
        <el-checkbox-group v-model="checkedRoles" @change="handleCheckedRolesChange">
          <el-checkbox v-for="role in roles" :label="role.id" :key="role.id">{{role.description}}</el-checkbox>
        </el-checkbox-group>
      </p>
      <p>
        <el-button type="primary" @click="submit()"></el-button>
      </p>

    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      loginname: "",
      password: "",
      phonenum: "",
      name: "",
      // roleidstr:'',
      roletype: 2,
      companyid: "",

      options: [],
      // left: [],
      // right: [],

      checkAll: false,
      checkedRoles: [],
      roles: [],
      isIndeterminate: true
    };
  },
  methods: {
    handleCheckAllChange(val) {
      if (val) {
        let roleids = [];
        this.roles.forEach(elment => {
          roleids.push(elment.id);
        });
        this.checkedRoles = roleids;
      } else {
        this.checkedRoles = [];
      }
      this.isIndeterminate = false;
    },

    handleCheckedRolesChange(value) {
      let checkedCount = value.length;
      if (this.roles.length == checkedCount) this.checkAll = true;
      else this.checkAll = false;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.roles.length;
    },

    submit() {
      console.log("submit");
      var _this = this;
      var p = this.checkvalues();
      console.log(p);
      p.then(
        function(res) {
          console.log("resolve:" + res);
          _this.adduser();
        },
        function(res) {
          console.log(res);
        }
      ).catch(err => {
        console.log(err);
      });
    },

    adduser() {
      //ID,
      // if(this.right.length>1){
      //     this.$message({
      //             type: 'error',
      //             message: ""
      //             });
      //     return false;
      // }

      let roleidstr = "";
      let params = new URLSearchParams();

      this.checkedRoles.forEach(element => {
        roleidstr += element + ",";
        if (element == 1) this.roletype = 1;
      });
      roleidstr = roleidstr.replace(/^,+/, "").replace(/,+$/, "");

      //ID

      this.checkedRoles.forEach(element => {
        roleidstr +=  ","+element;
      });
      if (this.checkedRoles.indexOf(1) != -1) this.roletype = 1;
      else this.roletype = 2;

      roleidstr = roleidstr.replace(/^,+/, "").replace(/,+$/, "");

      params.append("loginname", this.loginname);
      params.append("password", this.password);
      params.append("phonenum", this.phonenum);
      params.append("name", this.name);
      params.append("companyid", this.companyid);
      params.append("roleids", roleidstr);
      params.append("roletype", this.roletype);
      this.$axios({
        method: "post",
        url: "/api/user/adduser",
        params: params
      })
        .then(data => {
          //   console.log(data);
          if (data.data.code == 0) {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          } else {
            this.$message({
              type: "error",
              message: data.data.msg
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "error",
            message: err
          });
        });
    },

    checkvalues() {
      console.log("companyid:" + this.companyid);
      if (this.companyid != 1) {
        for (let element in this.checkedRoles) {
          if (this.checkedRoles[element] == 1) {
            this.$message({
              type: "error",
              message: ""
            });
            return Promise.reject(-1);
          }
        }
      }
      return Promise.resolve(1);
    }
  },

  created() {
    //
    let roletype = sessionStorage.getItem("roletype");
    let companyparams = new URLSearchParams();
    if (roletype != 1) {
      companyparams.append("companyid", sessionStorage.getItem("companyid"));
    }

    this.$axios.post("/api/company/getcompanylist", companyparams)
      .then(data => {
        let companylist = data.data.data;
        companylist.forEach(element => {
          if (element.isvalid == 1)
            //   this.left.push({label:element.companyname,key:element.id})
            this.options.push({
              label: element.companyname,
              key: element.id,
              value: element.id
            });
        });
      })
      .catch(err => {
        console.log(err);
        this.$message({
          type: "error",
          message: err
        });
      });
    //
    let params = new URLSearchParams();
    params.append("roletype", roletype);
    this.$axios({
      method: "post",
      url: "/api/role/rolelist",
      params: params
    })
      .then(data => {
        let rolelist = data.data.data;
        console.log(rolelist);
        this.roles = rolelist;
      })
      .catch(err => {
        console.log(err);
        this.$message({
          type: "error",
          message: err
        });
      });
  }
};
</script>
<style scope="scoped">
.myusertitle {
  display: inline-block;
  width: 80px;
  text-align: right;
  color: #606266;
}

.usernotice {
  font-size: 5px;
  width: 100%;
  text-align: left;
  color: red;
  margin-left: 8px;
}
</style>


